<template>
    <div class="">
        <!-- <div class="menu-bar">
            <v-select id="base" :options="currencyList[quote]['pairs']" :clearable="false" v-model="baseCurrency"
                      placeholder="Select Token"></v-select>
            <span class="slash">/</span>
            <v-select id="quote" :options="quoteOptions" :searchable="false" :clearable="false" v-model="quote"
                      @input="resetBase" style="width: 100px"></v-select>
            <button class="add-btn" @click="addCoinPair"><i class="fa fa-plus fa-lg" aria-hidden="true"></i></button>
        </div> -->
        <AdView></AdView>
        <div class="topboard">
            <div class = "togglediv">
                <span class="primary-text">Today's Cryto Prices</span>
                <bootstrap-toggle v-model="showTopBoard" :options="{ on: 'Yes', off: 'No' }" :disabled="false" />
                <span class="primary-text right">Highlight</span>
            </div>
            <div v-show="showTopBoard">
                <TopGainer></TopGainer>
                <TopLoser></TopLoser>
                <RecentAdd></RecentAdd>
            </div>
        </div>
        <CryptoBoard></CryptoBoard>
        <!-- <button class="clear-btn" @click="swap">Swap</button> -->
    </div>
</template>
<script>
    import coins from '@/assets/group.json'
    import AdView from '@/views/AdView.vue'
    import TopGainer from '@/components/TopGainer.vue'
    import TopLoser from '@/components/TopLoser.vue'
    import RecentAdd from '@/components/RecentAdd.vue'
    import CryptoBoard from '@/views/CryptoBoard.vue'
    // import axios from 'axios';
    import {isEmpty} from '../util/Utility'
    import {subscribeSymbol} from '../services/binance'
    import {mapState} from 'vuex'
    import BootstrapToggle from 'vue-bootstrap-toggle'

    export default {
        name: 'dashboard',
        data() {
            return {
                currencyList: coins,
                quote: 'BNB',
                baseCurrency: {},
                showTopBoard: true,
            }
        },
        mounted() {
            if (this.currencies) {
                this.currencies.forEach(currency => {
                    subscribeSymbol(currency.symbol);
                });
            }
            
        },
        computed: {
            ...mapState(['currencies']),
            quoteOptions() {
                return Object.keys(coins)
            }
        },
        components: {
            CryptoBoard,
            AdView,
            TopGainer,
            TopLoser,
            RecentAdd,
            BootstrapToggle,
        },
        methods: {
            resetBase() {
                this.baseCurrency = {}
            },
            clear() {
                localStorage.clear();
                location.reload();
            },
            addCoinPair() {
                if (!isEmpty(this.baseCurrency)) {
                    const symbol = `${this.baseCurrency.value}${this.quote}`;
                    subscribeSymbol(symbol);
                    this.$store.commit('ADD_COIN_PAIR', {
                        "symbol": symbol,
                        "base": this.baseCurrency.value,
                        "quote": this.quote,
                        "name": this.baseCurrency.name,
                        "cid": this.baseCurrency.cid
                    })
                }
            }
        }
    }
</script>
